.contact {
  color: rgb(38, 39, 39);
  text-shadow: 2px 2px 2px;
  text-align: center;
}
input[type="text"],
input[type="email"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
.container {
  margin: 100px 200px 100px 200px;
}
textarea[type="text"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
.btn:hover {
  color: white;
  background-color: black;
}
